import { StickerPlugin } from '@ali/4ever-bamboo';
import { StickerPluginConfig, ICustomSticker } from '../types';
import createRenderNode from '../renderNode';
import onCangjieSelect from '../handlers/onCangjieSelect';
import onAction from '../handlers/onAction';
import onKeyDown from '../handlers/onKeyDown';
import { DEFAULT_LANG, STICKER_NODE_TYPE } from '../constants';
import { BiPlugin, BiPluginConfig } from '../../../types';
import { moFactory } from '@ali/4ever-plugin-sticker';

const { DEFAULT_STICKERS, Sticker } = StickerPlugin;

function createStickerPlugin(config: BiPluginConfig): BiPlugin {
  const stickerConfig: StickerPluginConfig = {
    lang: DEFAULT_LANG,
    customSticker: [DEFAULT_STICKERS] as ICustomSticker,
    ...config.sticker,
  };
  const sticker = new Sticker(stickerConfig as StickerPlugin.IStickerProps);
  const params = {
    stickerConfig,
    sticker,
  };
  return {
    ...moFactory({}),
    // @ts-ignore
    renderNode: createRenderNode(params),
    onAction,
    onKeyDown,
    onCangjieSelect,
    models: {
      klass: 'inline',
      type: STICKER_NODE_TYPE,
    },
  };
}

createStickerPlugin.defaultSticker = DEFAULT_STICKERS;
export default createStickerPlugin;
